import axios from 'axios'
export default {
  name: 'checkProjectDetailForm',
  data () {
    return {
      project: '',
      okProject: false,
      taskList: [],
      name: '',
      startTime: '',
      endTime: '',
      content: '',
      target: '',
      managerName: ''
    }
  },
  methods: {
    goCheckProjectTask (id, name) {
      sessionStorage.setItem('taskID', id)
      sessionStorage.setItem('taskName', name)
      this.$router.push('/checkProjectTask')
    },
    getProject () {
      // axios.get('/static/testData/project.json')
      axios.get(this.url + '/project/id/' + sessionStorage.getItem('pid'))
        .then(this.getProjectSucc)
    },
    getProjectSucc (res) {
      res = res.data
      if (res) {
        this.project = res
        this.name = res.name
        this.startTime = res.startTime
        this.endTime = res.endTime
        this.content = res.content
        this.target = res.target
        this.managerName = res.managerName
      }
    },
    getTaskList () {
      // axios.get('/static/testData/module.json')
      axios.get(this.url + '/module/project/' + sessionStorage.getItem('pid'))
        .then(this.getTaskListSucc)
    },
    getTaskListSucc (res) {
      res = res.data
      if (res) {
        this.taskList = res
      }
    },
    submit () {
      this.$axios({
        method: 'put',
        url: this.url + '/project/',
        data: {
          id: parseInt(sessionStorage.getItem('pid')),
          state: this.Run,
          name: this.name,
          managerID: this.project.managerID,
          startTime: this.startTime,
          endTime: this.endTime,
          content: this.content,
          target: this.target
        }
      }).then((res) => {
        if (res.status === 200) {
          alert('提交成功！')
          this.$router.push('/check')
        } else alert(res.data)
      }).catch((err) => {
        alert(err)
      })
      this.okProject = false
    }
  },
  mounted () {
    this.getProject()
    this.getTaskList()
  }
}



